.section__heading {
    text-align: center;
    margin-bottom: 50px;
}

.contacter {
    padding: 80px 0;
    background-color: #fff;
}

.contacter h1 h3 h4 {
    font-family: 'Jost', sans-serif;
}

.txtbox{
    font-family: 'Jost', sans-serif;
}

.section__heading h2 {
    font-size: 1.8rem;
    font-weight: 200;
    text-transform: uppercase;
    color:#f95858;
}

.contacter .section__heading {
    margin-bottom: 50px;
    text-align: center;
}

.contact__form {
    font-family: 'Jost', sans-serif;
    margin: 0 auto;
    padding: 40px;
    background: #fff;
    border-radius: 10px;
    width: 600px;
    box-shadow: 0 10px 60px 0px rgb(0 0 0 / 20%);
}

.contact-form-control {
    height: 45px;
    border: #ccc solid 1px;
    margin-bottom: 30px;
}

textarea.contact-form-control {
    min-height: 100px;
}

input[type=text], select, textarea {
  width: 100%; /* Full width */
  padding: 12px; /* Some padding */ 
  border: 1px solid #ccc; /* Gray border */
  border-radius: 4px; /* Rounded borders */
  box-sizing: border-box; /* Make sure that padding and width stays in place */
  margin-top: 6px; /* Add a top margin */
  margin-bottom: 16px; /* Bottom margin */
  resize: vertical /* Allow the user to vertically resize the textarea (not horizontally) */
}

.btn-contact-form {
    display: center;
    width: 50%;
    padding: 10px;
    font-size: 18px;
    text-transform: uppercase;
    background: #f95858;
    color: #fff;
    transition: all linear 0.3s;
}

.btn-contact-form:hover {
    color: #fff;
}

.popup{
    margin-top: 5900px;
    width: 600px;
    height: 480px;
    background-color: #fda3a3;
    border-radius: 6px;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    text-align: center;
    padding: 0 30px 30px;
    color: #333;
}

.popup img{
    width:110px;
    margin-top: 30px;
    border-radius: 10%;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

.popup h2{
    font-size: 38px;
    font-weight: 500;
    margin: 10px 0 10px;
    color: #52b230;
}

.popup p{
    margin-top: 50px;
    font-size: 30px;
    color: #fff;
}

.popup button{
    width: 50%;
    margin-top:80px;
    padding: 10px 0;
    background-color: #52b230;
    color: #fff;
    border: 0;
    outline: none;
    font-size: 18px;
    border-radius: 4px;
    cursor:pointer;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

.popup button:hover{
    background-color: #61cf39;
}

