.about{
    width: 100%;
    display: contents;
    background-color: rgb(164, 170, 180);

    position: relative;
    transform: translate(-50%, -50%);
    top:50%;
    left:50%;
    text-align: center;
}

#apropos{
    background-color: rgb(21, 21, 21);
}

p {
    color: #fff;
    font-style: normal;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* Ajoutez une ombre portée */
    font-family: "Arial", sans-serif; 
    line-height: 1.5;
}

.boxes {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .row-apropos {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  
  .section-apropos {
    flex: 0 0 60%;
    text-align: left;
    padding: 20px;
  }
  
  .button-container {
    display: flex;
    margin-top: 50px;
  }


  .btn-apropos {
    background-color: #f95858;
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-size: 18px;
    border: 1px solid transparent;
    padding: 20px 20px;
    border-radius: 3px;
    transition: transform 0.3s ease-in-out; /* Ajuster la durée de transition selon vos préférences */
    margin-right: 50px;
    
  }
  
  .btn-apropos:hover {
    cursor: pointer;
    transform: scale(1.1);
  }
  

.titre_apropos {
    font-size: 2.5rem;
    font-weight: 200;
    text-transform: uppercase;
    color:#f95858;
    margin-bottom: 80px;
    margin-top:-20px;
}

.double-contenu {
    display: flex;
    justify-content: space-between;
  }
  
  .contenu-gauche {
    flex-basis: 50%;
  }
  
  .contenu-droite {
    flex-basis: 80%;
    /* margin-right: 60px; */
  }

@media screen and (max-width: 640px){
    .about h1{
        font-size: 3rem;
    }

    .about p{
        font-size: 1.4rem;
    }
}