.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 70px;
  background-color: #000000ed;
  top: 0;
  width: 100%;
  /* position: fixed; */
  border-bottom: 1px solid #c6c6c6;
}

.logo a {
  font-size: 26px;
  font-weight: 400;
  color: #ffffff;
  text-decoration: none;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.menu-icon {
  display: none;
}

.nav-elements {
}

.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.nav-elements ul li:not(:last-child) {
  margin-right: 60px;
}

.nav-elements ul a {
  /* font-family: 'Source Sans Pro', sans-serif; */
  font-size: 16px;
  font-weight: bold;
  /* font-weight: 400; */
  color: #ffffff;
  text-decoration: none;
}

.nav-elements ul a:hover{
    color: #f95858;
}

.nav-elements ul a.active {
  color: #f95858;
  font-weight: 500;
  position: relative;
}

.nav-elements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #f95858;
}

.btn{
  background-color: #f95858;
  color: rgb(255, 255, 255);
  text-decoration: none;
  border: 1px solid transparent;
  padding: 5px 8px;
  border-radius: 5px;
  transition: transform 0.3s ease-in-out; /* Ajuster la durée de transition selon vos préférences */
  margin-right: 50px;
}
    
.btn:hover{
cursor: pointer;
transform: scale(1.1);
}

.flag{
    size: 10px;
}

section{
  padding-top: 80px;
  margin-top: 8rem;
  height:100vh;
}

.floatbar a{
  size: 0px;
  visibility: hidden;
}



@media (max-width: 768px) {
  .nav-elements ul li:not(:last-child) {
    margin-right: 30px;
  }
}

/* responsive tablette */
@media (max-width: 1200px) {
    .navbar {
      position: relative;
    }

    .menu-icon {
      display: block;
      cursor: pointer;
    }
  
    .nav-elements {
      position: relative;
      right: 0;
      top: 60px;
      background-color: #ffffff;
      width: 0px;
      height: calc(100vh - 60px);
      transition: all 0.3s ease-in;
      overflow: hidden;
    }
  
    .nav-elements.active {
      width: 270px;
    }
  
    .nav-elements ul {
      display: flex;
      flex-direction: column;
    }
  
    .nav-elements ul li {
      margin-right: unset;
      margin-top: 22px;
    }

    /* Float bar*/
.floatbar {
  background-color: rgba(0, 0, 0, 0.4);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

.floatbar a {
  background: transparent;
  padding: 0.6rem;
  border-radius: 50%;
  display: flex;
  color: rgb(214, 214, 214);
  font-size: 1.6rem;
  visibility: visible;
}

.floatbar a:hover {
  background-color: rgba(0, 0, 0, 0.3);
  color: #f95858;
}

.floatbar a.active {
  color: #f95858;
}
  }


/* responsive mobile */
@media (max-width: 600px) {
  .navbar {
    position: relative;
  }

  .menu-icon {
    display: block;
    cursor: pointer;
  }

  .nav-elements {
    position: relative;
    right: 0;
    top: 60px;
    background-color: #ffffff;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .nav-elements.active {
    width: 270px;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }

  /* Float bar*/
