.mask{
    width: 100%;
    height: 100vh;
    position: relative;
    top: -22px;
}

.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.7;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top:50%;
    left:50%;
    text-align: left;
}

.hero .content h1 {
    font-size: 72px;
    font-family: 'Poppins', sans-serif;
    /* padding: 0.6rem 0 0rem; */
    color: #fff;
}

.hero .content h2 {
    font-size: 52px;
    font-family: 'Source Sans Pro', sans-serif;
    /* padding: 0.6rem 0 0rem; */
    color: #fff;
}

.hero .content h3{
    font-size: 22px;
    font-family: "Lucida Console", monospace;
    padding: 0.6rem 0 1.5rem;
    color: #fff;
}

.hero .content p{
    font-size: 52px;
    font-weight: 200;
    font-family: "Lato";
    /* text-transform: uppercase; */
    color:#f95858;
}

.testFont{
    font-family: 'Shadows Into Light', cursive;
}

.emojiHand {
    font-size:  72px;
    animation: waving-hand 2.5s infinite;
    transform-origin: 70% 70%;
    display: inline-block;
}

.arrow-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -200px; /* Ajustez la hauteur selon vos besoins */
  }
  
  .arrow-down {
    width: 40px; /* Ajustez la largeur de la flèche selon vos besoins */
    height: 40px; /* Ajustez la hauteur de la flèche selon vos besoins */
    border: solid #ffffff;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
    border-radius: 3px; /* Ajout de coins arrondis */
    animation: arrowBounce 1s infinite ease-in-out;
  }
  
  @keyframes arrowBounce {
    0% {
      transform: translateY(0) rotate(45deg);
    }
    25% {
      transform: translateY(10px) rotate(45deg);
    }
    50% {
      transform: translateY(0) rotate(45deg);
    }
    75% {
      transform: translateY(-10px) rotate(45deg);
    }
    100% {
      transform: translateY(0) rotate(45deg);
    }
  }
  
  
  


@keyframes waving-hand {
    0% {
        transform: rotate(0deg);
    }
    10% {
        transform: rotate(14deg);
    }
    20% {
        transform: rotate(-8deg);
    }
    30% {
        transform: rotate(14deg);
    }
    40% {
        transform: rotate(-4.0deg);
    }
    50% {
        transform: rotate(10deg);
    }
    60% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@media screen and (max-width: 640px){
    .mask{
        position: relative;
        top:0px;
    }
    .hero .content h2 {
        font-size: 3rem;
    }

    .hero .content h3{
        font-size: 1rem;
    }

    .hero .content p{
        font-size: 1.4rem;
    }
}

/* responsive tablette */
@media (max-width: 1200px) {
    .mask{
        position: relative;
        top:0px;
    }
    .hero .content h2 {
        font-size: 3rem;
    }

    .hero .content h3{
        font-size: 2rem;
    }

    .hero .content p{
        font-size: 1.4rem;
    }
}